.contactpage{
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.contactpagetitle{
    font-size: 2rem;
    margin-bottom: 1rem;
}
.clientdesc{
    font-weight: 300;
    font-size: 1rem;
}
.clientimages{
    margin: 1rem 0;
    display: flex;
    flex-wrap: wrap;

}
.clientimg{
    object-fit: cover;
    width: 23%;
    min-width: 9rem;
    margin: auto;
    padding: 0.25rem;

}
.clients{
    padding: 2rem;

}
.contactdesc{
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}
.contactform{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}
.name,.email,.message{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: rgb(99,8,8);

}
.submitbutton{
    background-color: white;
    border: none;
    padding: 0.75rem 3.5rem;
    border: 0.5rem;
    margin: 2rem;
}
.links{
    display: flex;
    flex-wrap: wrap;
}
.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;

}
.contect{
    display: flex;
    flex-direction: column;

}
.college{
    font-size: 1rem;
}
.active{
    color:blue;
    border-bottom: 3px solid blue;
    padding-bottom: 0.5rem;
}